<template>
  <div class="container">
    <div>
      <h1>LCC to LC Source Parameter Mapping</h1>
      <form>
        <label for="lcc">LCC Parameters</label>
        <textarea v-model="lcc"></textarea>

        <label for="lcn">LC JSON</label>
        <textarea v-model="lcn"></textarea>

        <button class="button" v-on:click="convert">Convert to LC</button>
        <button class="button button-outline" v-on:click="clear">Clear</button>
      </form>
    </div>
    <div>
      <h1>JSON to Dot Notation</h1>
      <form>
        <label for="json">JSON String</label>
        <textarea v-model="json"></textarea>

        <label for="dot">Dot Notation</label>
        <textarea v-model="dot"></textarea>

        <button class="button" v-on:click="convert_json">Convert JSON</button>
        <button class="button button-outline" v-on:click="clear_json">Clear</button>
      </form>
    </div>
  </div>
</template>

<script>
const dot = require('dot-object');

export default {
  name: 'app',
  data () {
    return {
      lcc: "",
      lcn: "",
      json: "",
      dot: ""
    }
  },
  methods: {
    convert: function(event) {
      let converted_lcc = [];
      event.preventDefault();
      console.log(this.lcc);
      var split = this.lcc.split("&");
      split.forEach( (element) => {
        let fields = element.split("=")
        let value = fields[1].slice(2, -1)
        let property = fields[0]
        if (value !== property) {
          converted_lcc.push(`{\n  "value":"{{${value}}}",\n  "property":"${property}"\n}`)
        }
      })
      this.lcn = converted_lcc.join(",\n");
    },
    clear: function(event) {
      event.preventDefault();
      this.lcc = "";
      this.lcn = "";
    },
    convert_json: function(event) {
      event.preventDefault();
      let str = "";
      let obj = JSON.parse(this.json);

      let tgt = dot.dot(obj);
      console.log(tgt);

      Object.keys(tgt).forEach(function(key){
        str = str + key + "\n";
      })
      this.dot = str;
    },
    clear_json: function(event) {
      event.preventDefault();
      this.json = "";
      this.dot = "";
    }
  }
}
</script>

<style>

</style>



